import React from 'react';
import SessionMedia from './SessionMedia';
import { ReactComponent as ArrowIcon } from '../assets/svg/arrow-left.svg';
import { ReactComponent as LockIcon } from '../assets/svg/lock.svg';
import { ReactComponent as UnlockIcon } from '../assets/svg/unlock.svg';

class Sessions extends React.Component {
	constructor(props) {
		super(props);
	}

	formatAudio(channels) {
		switch (channels) {
			case '1':
				return '(Mono)';
				break;
			case '2':
				return '(Stereo)';
				break;
			case '5':
				return '(5.1)';
				break;
			case '6':
				return '(6.1)';
				break;
			case '7':
				return '(7.1)';
				break;
			default:
				return `(${channels})`;
		}
	}

	playbackDetails(selectedMedia, type, session) {
		if (!selectedMedia.Part) {
			return null;
		}
		let bitrate;
		let typeFormat;
		switch (type) {
			case 'unknown':
				typeFormat = 'Unknown';
				break;
			case 'directplay':
				typeFormat = 'Direct Play';
				break;
			case 'transcode':
				if (session.TranscodeSession) {
					if (session.TranscodeSession.videoDecision === 'copy') {
						typeFormat = 'Direct Stream';
					} else if (
						session.TranscodeSession.videoDecision === 'transcode'
					) {
						typeFormat = `Transcode
									(${session.TranscodeSession.transcodeHwEncodingTitle})`;
					} else {
						typeFormat = 'Unknown Transcode';
					}
				} else {
					typeFormat = '...';
				}
				break;
			default:
				typeFormat = type;
		}

		bitrate = selectedMedia.Part[0].Stream[0].bitrate;
		if (bitrate > 1000) {
			bitrate = Math.ceil(bitrate / 1000) + ' mbps';
		} else {
			bitrate += ' kbps';
		}

		return (
			<>
				<p>{typeFormat}</p>
				<p>
					<strong>Video</strong>
				</p>
				{selectedMedia.Part[0].Stream[0].decision === 'transcode' ||
				selectedMedia.Part[0].Stream[0].decision === 'copy' ? (
					<>
						<p className="session--container">
							<span className="session--label">Container:</span>
							<span className="upper">
								{selectedMedia.container}
							</span>
							<ArrowIcon />
							<span className="upper">
								{selectedMedia.Part[0].container}
							</span>
						</p>
						<p>
							<span className="session--label">Quality:</span>
							{bitrate +
								' ' +
								selectedMedia.Part[0].Stream[0]
									.extendedDisplayTitle}
						</p>
					</>
				) : (
					<>
						<p className="session--container">
							<span className="session--label">Container:</span>
							<span className="upper">
								{selectedMedia.Part[0].container}
							</span>
						</p>
						<p>
							<span className="session--label">Quality:</span>
							{bitrate +
								' ' +
								selectedMedia.Part[0].Stream[0]
									.extendedDisplayTitle}
						</p>
					</>
				)}

				<p>
					<strong>Audio</strong>
				</p>
				{selectedMedia.Part[0].Stream[1].decision === 'transcode' ? (
					<p>
						{selectedMedia.Part[0].Stream[1].extendedDisplayTitle}
						<ArrowIcon />
						<span className="upper">
							{selectedMedia.Part[0].Stream[1].codec}
							&nbsp;
						</span>

						{this.formatAudio(
							selectedMedia.Part[0].Stream[1].channels
						)}
					</p>
				) : (
					<p>
						{selectedMedia.Part[0].Stream[1].extendedDisplayTitle}
					</p>
				)}
				<p>
					<strong>Player</strong>
				</p>
				<p>
					{session.Player.title + ' (' + session.Player.product + ')'}
				</p>
				<p>
					{session.Player.local ? 'Local' : 'Remote'}{' '}
					{session.Player.address}{' '}
					<div
						className={
							'session--secure secure-' + session.Player.secure
						}
					>
						{session.Player.secure ? <LockIcon /> : <UnlockIcon />}
					</div>
				</p>
			</>
		);
	}

	render() {
		if (this.props.sessions) {
			let sessions = this.props.sessions;
			if (sessions.length === 0) {
				return <p>No active sessions</p>;
			} else {
				return (
					<div className="sessions">
						{sessions.map((session) => {
							let media = null;
							let selectedMedia = false;
							session.Media.map((item) => {
								if (item.selected) {
									selectedMedia = item;
								}
							});
							let playback = selectedMedia.Part
								? selectedMedia.Part[0].decision
								: 'unknown';
							switch (session.type) {
								case 'episode':
									media = session.grandparentRatingKey;
									break;
								case 'movie':
									media = session.ratingKey;
									break;
							}
							return (
								<div
									className="session"
									data-id={
										session.Session
											? session.Session.id
											: 'pending'
									}
									key={
										session.Player.machineIdentifier +
										session.ratingKey +
										'__session'
									}
								>
									<SessionMedia
										id={media}
										uid={
											session.Player.machineIdentifier +
											session.ratingKey
										}
										type={session.type}
										userThumb={session.User.thumb}
										live={session.live ? true : false}
										progress={
											session.Media.length > 0 &&
											session.viewOffset
												? (session.viewOffset /
														session.Media[0]
															.duration) *
												  100
												: 0
										}
										playbackState={session.Player.state}
									/>
									<div className="session--info">
										<p className="session--title">
											{session.title +
												(session.live ? ' (Live)' : '')}
										</p>
										{session.type === 'episode' ? (
											<p className="session--show">
												{session.grandparentTitle +
													(session.parentTitle
														? ' | ' +
														  session.parentTitle
														: '')}
											</p>
										) : null}
										<div className="session--playback">
											<p className="session--title">
												Playback Info
											</p>
											{this.playbackDetails(
												selectedMedia,
												playback,
												session
											)}
										</div>
										<hr />
									</div>
								</div>
							);
						})}
					</div>
				);
			}
		} else {
			return <p>No active sessions</p>;
		}
	}
}

export default Sessions;
